import React, { Component } from "react";
import Swal from 'sweetalert2';
import { HOST_DOMAIN } from '../../../../../../Services/Api'
import { baseUrl } from "../../../../../../Assets/AssetsHelper";
import SocialLoginFacebook from '../../../../SocialLogin/Facebook'
import SocialGoogleLogin from '../../../../SocialLogin/YouTube'
import { GET_NETWORK_ICON } from '../../../../../../Services/Utils/utils'
import { twitterConnect, flickrConnect, yammerConnect, tumblerConnect, vimeoConnect, facebookConnect } from '../../../../SocialLogin/auth'
import { sendPublishRequest, getPublishList, publishDeleteRequest, getInstagramDetails, InstagramPostPublish, getFacebookDetails, FacebookPublish } from '../../../../../../Services/Actions/publishActions'

class GetRightsSocialAccount extends Component {
    state = {
        connected: null,
        showConfirm: false
    }
    componentDidMount() {
        const { modalAssetData } = this.props
        const channel = new BroadcastChannel('connect');
        var self = this;
        channel.addEventListener("message", (event) => {
            const responseToken = JSON.parse(event.data);
            console.log("responseToken GetRightsSocialAccount GetRightsSocialAccount", responseToken)
            if (modalAssetData.networkId == 2 || modalAssetData.networkId == 18) {
                getInstagramDetails({ access_token: responseToken.data.access_token }).then((response) => {
                    const { accounts } = response.data
                    console.log("response.data", response.data, accounts)
                    var accountsData = [];
                    if (accounts && accounts.length > 0) {
                        accounts.map((item) => {
                            accountsData.push({ id: item.id, name: item.username, picture: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                        })
                    }

                    self.setState({ access_token: responseToken.data.access_token, socialData: response.data, accountsData }, () => this.props.onUpdateData("instagram", accountsData))
                })
            }
            else if (modalAssetData.networkId == 3) {
                getFacebookDetails({ access_token: responseToken.data.access_token }).then((response) => {
                    const { pages } = response.data
                    console.log("response.data", response.data, pages)
                    var accountsData = [];
                    if (pages && pages.length > 0) {
                        pages.map((item) => {
                            accountsData.push({ id: item.id, name: item.name, access_token: item.access_token, picture: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                        })
                    }

                    self.setState({ access_token: responseToken.data.access_token, socialData: response.data, accountsData }, () => this.props.onUpdateData("facebook", accountsData))
                })
            }
            else if (modalAssetData.networkId == 1) {
                getInstagramDetails({ access_token: responseToken.data.access_token }).then((response) => {
                    const { accounts } = response.data
                    console.log("response.data", response.data, accounts[0].id)
                    var accountsData = [];
                    if (accounts && accounts.length > 0) {
                        accounts.map((item) => {
                            accountsData.push({ id: item.id, name: item.username, picture: baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`) })
                        })
                    }

                    self.setState({ access_token: responseToken.data.access_token, socialData: response.data, accountsData }, () => this.props.onUpdateData("twitter", accountsData))
                })
            }

        });

    }

    handleYes = event => {
        this.setState({ showConfirm: true })
    }
    handleNo = event => {
        this.setState({ showConfirm: true })
    }
    onClickSocialConnect = (data) => event => {
        if (data && Object.keys(data).length > 0) {
            Swal.fire({
                imageUrl: baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(data.networkId)}.svg`),
                title: `<strong>${GET_NETWORK_ICON(data.networkId)}</strong>`,
                html: `Connect to ${GET_NETWORK_ICON(data.networkId)} and start collecting assets.`,
                showCloseButton: true,
                showCancelButton: false,
                focusConfirm: true,
                confirmButtonText: `Continue with ${GET_NETWORK_ICON(data.networkId)}`,
                confirmButtonAriaLabel: `Continue with ${GET_NETWORK_ICON(data.networkId)}`,
                cancelButtonText: '<i className="fa fa-thumbs-down"></i>',
                cancelButtonAriaLabel: 'Thumbs down'
            }).then((result) => {
                localStorage.setItem("socialConnect", true);
                localStorage.setItem("socialNetworkId", data.networkId)
                switch (data.networkId) {
                    case 1:
                        twitterConnect(true);
                        break;
                    case 6:
                        flickrConnect(6);
                        break;
                    case 21:
                        yammerConnect("yammer", null, true);
                        break;
                    case 11:
                        tumblerConnect(11);
                        break;
                    case 8:
                        vimeoConnect(true);
                        break;
                    case 3:
                        facebookConnect(3, true);
                        break;
                    case 2:
                        const url = `https://api.instagram.com/oauth/authorize?client_id=${'480810795949098'}&redirect_uri=${HOST_DOMAIN}/instagram/feeds/authCallback&scope=user_profile,user_media&response_type=code`
                        window.open(url, '_blank', "width=786,height=786");
                        break;
                    case 18:
                        facebookConnect(18, true)
                        break;
                    default:
                        break;
                }
            });
        }
    }


    render() {
        const { showConfirm } = this.state
        const { modalAssetData, connectedAccount } = this.props;
        console.log("modalAssetData social account", this.props)
        return <><div className="p-lg-6 p-3">
            <div className="m-auto">
                {
                    connectedAccount && connectedAccount.data.length > 0 ? <><h3 className="mb-2 pb-2 fs-7 pt-1">
                        <span className="fw-medium">Connect Account</span>
                    </h3>
                        <div className="mb-8">
                            <button type="button" className="d-flex align-items-center rounded-1 mb-2 p-3 border border-dark bg-white w-100">
                                <div className="d-flex align-items-center flex-grow-1 w-75">
                                    <div className="symbol symbol-40 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                                        <img className="object-center-cover" src={connectedAccount.data[0].picture} height={40} width={40} alt="instagram" />
                                    </div>
                                    <div className="w-75 overflow-hidden">
                                        <span className="text-start fs-7 d-block fw-bolder text-dark text-ellipsis">@{connectedAccount.data[0].name}</span>
                                    </div>
                                </div>
                                <div className="d-flex">
                                    <span className="h-20px w-20px">
                                        <img className="object-center-cover" src={baseUrl(`media/icons/social-icons/${connectedAccount.provider}.svg`)} alt="" />
                                    </span>
                                </div>
                            </button>
                            <span className="fw-medium">You are logged in with this account</span>
                        </div></> : <>{
                            modalAssetData.networkId == 3 ? <SocialLoginFacebook onUpdateData={this.props.onUpdateData} /> : modalAssetData.networkId == 7 ? <SocialGoogleLogin onUpdateData={this.props.onUpdateData} /> : <button type="button" className="d-flex align-items-center rounded-1 mb-2 p-3 btn btn-outline-secondary w-100" onClick={this.onClickSocialConnect(modalAssetData)}>
                                <div className="d-flex align-items-center flex-grow-1 w-75">
                                    <div className="symbol symbol-40 bg-light rounded-circle flex-shrink-0 me-2 overflow-hidden"></div>
                                    <div className="w-75 overflow-hidden">
                                        <span className="text-start fs-7 d-block fw-bolder text-ellipsis">Add Account</span>
                                    </div>
                                </div>
                                {modalAssetData.networkId ?
                                    <div className="d-flex">
                                        <span className="h-20px w-20px">
                                            <img className="object-center-cover" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalAssetData.networkId)}.svg`)} alt="network" style={{ filter: 'grayscale(1)', opacity: '.5' }} />
                                        </span>
                                    </div>
                                    : null}
                            </button>
                        }</>
                }

            </div>
        </div>


        </>
    }
}

export default GetRightsSocialAccount;